@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Geist+Mono:wght@100..900&display=swap');

@layer components {
  .input-base {
    @apply mt-1 p-2 w-full bg-customBg border border-current 
           focus:outline-none focus:ring-1 focus:ring-white text-white;
  }

  .table-cell {
    @apply border border-lime-400 px-4 py-2 bg-customBg font-light;
  }

  .btn-custom {
    @apply bg-customBg border border-inherit hover:bg-gray-700 
           text-white py-2 px-4 font-light;
  }
}
